<template>
  <div>
    <p>Format String In SQL</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <div>
      <textarea v-model="before_string" id="before_string" rows="13" cols="30" placeholder="input list of string"></textarea>
      <textarea v-model="after_string" id="after_string" rows="13" cols="30" placeholder="show formatted string" readonly></textarea>
    </div>
    <div>
      <input value="format string" type="button" @click="format_string()">
      <input value="clean data" type="button" @click="clean()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'AroundStringView',
  data () {
    return {
      before_string: '',
      after_string: ''
    }
  },
  methods: {
    format_string () {
      // clean history
      this.after_string = ''
      const beforeString = this.before_string.split('\n')
      let afterString = ''
      beforeString.forEach((element) => {
        afterString = afterString + '\'' + element + '\',' + '\n'
      })
      afterString = afterString.substr(0, afterString.length - 2)
      this.after_string = afterString
    },
    clean () {
      this.before_string = ''
      this.after_string = ''
    }
  }
}
</script>
